.footer {
  background-color: @grey-100;
}

.footer__wrapper {
  display: flex;
  flex-direction: column;
  padding-top: 39px;
  padding-bottom: 32px;
}

@media (min-width: 768px) {
  .footer__wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-top: 60px;
    padding-bottom: 66px;
    align-items: center;
  }
}

@media (min-width: 1440px) {
  .footer__wrapper {
    padding-top: 63px;
    padding-bottom: 61px;
  }
}

.footer__location {
  background-color: @basic-white;
}

.footer__address {
  width: 87%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  padding-top: 28px;
  padding-bottom: 28px;
}

@media (min-width: 768px) {
  .footer__address {
    width: 92%;
    padding-top: 66px;
    padding-bottom: 51px;
    box-sizing: border-box;
    padding-right: 40px;
    padding-left: 40px;
  }
}

.footer__address h2 {
  font-size: 16px;
  line-height: 18px;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  width: 140px;
}

@media (min-width: 768px) {
  .footer__address h2 {
    font-size: 20px;
    line-height: 21px;
    width: 159px;
  }
}

.footer__address p {
  font-size: 14px;
  line-height: 20px;
  color: @dark-grey-100;
  margin: 0;
  padding: 0;
  width: 140px;
  text-align: right;
}

@media (min-width: 768px) {
  .footer__address p {
    font-size: 16px;
    line-height: 20px;
    width: 159px;
  }
}

.footer__logo {
  margin: 0 auto;
}

@media (min-width: 768px) {
  .footer__logo {
    margin: 0;
  }
}

.social {
  position: relative;
  margin: 0 auto;
  padding: 0;
  list-style: none;
  width: 130px;
  display: flex;
  flex-wrap: wrap;
  padding-top: 21px;
  padding-bottom: 23px;
  align-items: center;
}

@media (min-width: 768px) {
  .social {
    margin: 0;
  }
}

.footer__social-line {
  margin-top: 32px;
  border-top: 1px solid @grey-600;
  border-bottom: 1px solid @grey-600;
}

@media (min-width: 768px) {
  .footer__social-line {
    margin: 0;
    border: none;
  }
}

.social__item {
  margin-right: 30px;
}

.social__item:nth-child(1) {
  margin-right: 36px;
}

.social__item:nth-child(2) {
  margin-right: 33px;
}

.social__item:nth-child(3n) {
  margin-right: 0;
}

.footer__copyrigh-link {
  font-size: 16px;
  line-height: 18px;
  color: @dark-grey-100;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 22px;
}

@media (min-width: 768px) {
  .footer__copyrigh-link {
    margin: 0;
  }

  .footer__copyrigh-link p {
    padding: 0;
    margin: 0;
    margin-right: 22px;
  }
}
